import Header from './Header'

const layoutStyle = {
    leftmargin: 100,
  margin: 200,
  padding: 20,
  border: '1px solid #F00'
}

const Layout = (props) => (
  <div style={layoutStyle}>
    <Header />
    {props.children}
  </div>
)

export default Layout


